<template>
    <div class="p-2 container-fluid">
        <b-col class="d-flex flex-column">
            <div>
                <h2 class="p-1">
                    <b-badge>{{ get_time_str() }}</b-badge>
                    {{ map_name }}
                    <b-button v-b-toggle.config size="sm" class="mb-2 float-right">
                        <b-icon icon="gear-fill" aria-hidden="true"></b-icon>
                        Settings
                    </b-button>
                </h2>
                <b-collapse id="config">
                    <b-form-group label="Shown Columns" label-cols-lg="2" v-slot="{ ariaDescribedby }">
                        <b-form-checkbox v-for="i in fields" :key="i.key" v-model="i.enabled" :aria-describedby="ariaDescribedby" inline>
                            {{ i.label ? i.label : i.key }}
                        </b-form-checkbox>
                    </b-form-group>
                    <b-form-group label="Table Options" label-cols-lg="2" v-slot="{ ariaDescribedby }">
                        <b-form-checkbox v-model="config.stickyHeader" :aria-describedby="ariaDescribedby" inline>Sticky Header</b-form-checkbox>
                        <b-form-checkbox v-model="config.striped" :aria-describedby="ariaDescribedby" inline>Striped</b-form-checkbox>
                        <b-form-checkbox v-model="config.bordered" :aria-describedby="ariaDescribedby" inline>Bordered</b-form-checkbox>
                        <b-form-checkbox v-model="config.borderless" :aria-describedby="ariaDescribedby" inline>Borderless</b-form-checkbox>
                        <b-form-checkbox v-model="config.outlined" :aria-describedby="ariaDescribedby" inline>Outlined</b-form-checkbox>
                        <b-form-checkbox v-model="config.small" :aria-describedby="ariaDescribedby" inline>Small</b-form-checkbox>
                        <b-form-checkbox v-model="config.hover" :aria-describedby="ariaDescribedby" inline>Hover</b-form-checkbox>
                        <b-form-checkbox v-model="config.dark" :aria-describedby="ariaDescribedby" inline>Dark</b-form-checkbox>
                        <b-form-checkbox v-model="config.fixed" :aria-describedby="ariaDescribedby" inline>Fixed</b-form-checkbox>
                        <b-form-checkbox v-model="config.footClone" :aria-describedby="ariaDescribedby" inline>Foot Clone</b-form-checkbox>
                        <b-form-checkbox v-model="config.noCollapse" :aria-describedby="ariaDescribedby" inline>No border collapse</b-form-checkbox>
                    </b-form-group>

                    <b-form-group label="Head Variant" label-cols-lg="2" v-slot="{ ariaDescribedby }">
                        <b-form-radio-group
                            v-model="config.headVariant"
                            :aria-describedby="ariaDescribedby"
                            class="mt-lg-2"
                        >
                            <b-form-radio :value="null" inline>None</b-form-radio>
                            <b-form-radio value="light" inline>Light</b-form-radio>
                            <b-form-radio value="dark" inline>Dark</b-form-radio>
                        </b-form-radio-group>
                    </b-form-group>

                    <b-form-group label="Table Variant" label-for="table-style-variant" label-cols-lg="2">
                        <b-form-select
                            id="table-style-variant"
                            v-model="config.tableVariant"
                            :options="config.tableVariants"
                        >
                            <template #first>
                                <option value="">-- None --</option>
                            </template>
                        </b-form-select>
                    </b-form-group>
                </b-collapse>
            </div>
            <b-table
                class="flex-grow-1"
                :sticky-header="config.stickyHeader?'500px':false"
                :striped="config.striped"
                :bordered="config.bordered"
                :borderless="config.borderless"
                :outlined="config.outlined"
                :small="config.small"
                :hover="config.hover"
                :dark="config.dark"
                :fixed="config.fixed"
                :foot-clone="config.footClone"
                :no-border-collapse="config.noCollapse"
                :head-variant="config.headVariant"
                :table-variant="config.tableVariant"
                :items="items" :fields="fields.filter(x=>x.enabled)"/>
        </b-col>
    </div>
</template>


<script>
export default {
    data() {
        return {
            fields: [
                {
                    key: 'job',
                    sortable: false,
                    enabled: true,
                },
                {
                    key: 'name',
                    sortable: false,
                    enabled: true,
                },
                {
                    key: 'dps',
                    sortable: true,
                    enabled: true,
                },

                {
                    key: 'dps_m',
                    label: 'Dps(1min)',
                    sortable: true,
                    enabled: true,
                },
                {
                    key: 'dmg',
                    label: 'Total Damage',
                    sortable: true,
                    enabled: false,
                },
                {
                    key: 'death',
                    sortable: true,
                    enabled: true,
                },
                // {
                //     key: 'dps_sd',
                //     label: 'Dps模拟dot',
                //     sortable: true,
                //     enabled: false,
                // },
                // {
                //     key: 'dmg_sd',
                //     label: '模拟dot总伤',
                //     sortable: true,
                //     enabled: false,
                // },
                // {
                //     key: 'dmg_sd_g',
                //     label: '模拟dot分布',
                //     enabled: false,
                // },
            ],
            items: [],
            min_record_time: 0,
            last_record_time: 0,
            map_name: "?",
            config: {
                tableVariants: [
                    'primary',
                    'secondary',
                    'info',
                    'danger',
                    'warning',
                    'success',
                    'light',
                    'dark'
                ],
                stickyHeader: true,
                striped: false,
                bordered: false,
                borderless: false,
                outlined: false,
                small: false,
                hover: true,
                dark: false,
                fixed: false,
                footClone: false,
                headVariant: null,
                tableVariant: '',
                noCollapse: false
            }
        }
    },
    methods: {
        set_data: function (min_record_time, last_record_time, map_name, data) {
            this.items = data
            if (min_record_time / 1000 !== this.min_record_time)
                this.min_record_time = min_record_time / 1000;
            if (last_record_time / 1000 !== this.last_record_time)
                this.last_record_time = last_record_time / 1000;
            if (map_name !== this.map_name)
                this.map_name = map_name;
        },
        get_time_str() {
            let d = this.last_record_time - this.min_record_time;
            return (d / 60).toFixed(0).padStart(2, "0") + ":" + (d % 60).toFixed(0).padStart(2, "0")
        }
    },
    mounted() {
        window['set_data'] = this.set_data
        this.set_data(
            0, 0, "some where", [
                {'job': '写bug的', 'name': 'NY',},
                {'job': '广告位1', 'name': '招租',},
                {'job': '广告位2', 'name': '招租',},
                {'job': '广告位3', 'name': '招租',},
                {'job': '广告位4', 'name': '招租',},
                {'job': '广告位5', 'name': '招租',},
                {'job': '广告位6', 'name': '招租',},
                {'job': '广告位7', 'name': '招租',},
            ]
        )
    }
}
</script>
